import myAxios from './axios';

// 
export function uploadImageAPI(paramsList) {
  return myAxios({
    url: '/uploadimg',
    method: 'post',
    data: paramsList,
  })
}

// 
export function deleteImageAPI(paramsList) {
  return myAxios({
    url: '/deleteimg',
    method: 'post',
    data: paramsList,
    async: false
  })
}
