<template>
  <div name="PostArticle" class="PostArticle-wrap">
    <el-row style="margin: 10px 0 50px 50px" type="flex">
      <font class="title"
        >&emsp;<span></span></font
      >
    </el-row>

    <el-form :model="ruleForm" :rules="rules" ref="ruleForm">
      <div class="forms">
        <el-row
          type="flex"
          justify="space-between"
          style="width: 600px; margin-bottom: 50px"
        >
          <font></font>
          <div
            style="display: flex; width: 400px; justify-content: space-between"
          >
            <el-radio
              style="width: 100px"
              v-model="ruleForm.article_status"
              label="1"
              border
              ></el-radio
            >
            <el-radio
              style="width: 100px"
              v-model="ruleForm.article_status"
              label="2"
              border
              ></el-radio
            >
          </div>
        </el-row>

        <el-row
          v-if="ruleForm.article_status == 1"
          type="flex"
          justify="space-between"
          style="width: 600px"
        >
          <font><span>*&nbsp;</span></font>
          <el-form-item prop="article_link">
            <el-input
              style="width: 400px; height: 50px"
              class="public-uncheck"
              v-model="ruleForm.article_link"
              placeholder="()"
              clearable
            ></el-input>
          </el-form-item>
        </el-row>

        <el-row type="flex" justify="space-between" style="width: 600px">
          <font><span>*&nbsp;</span></font>
          <el-form-item prop="article_title">
            <el-input
              style="width: 400px; height: 50px"
              class="public-uncheck"
              v-model="ruleForm.article_title"
              placeholder=""
              clearable
              :maxlength="20"
              show-word-limit
            ></el-input>
          </el-form-item>
        </el-row>

        <el-row type="flex" justify="space-between" style="width: 600px">
          <font><span>*&nbsp;</span></font>
          <el-cascader
            style="width: 400px; height: 70px"
            v-model="ruleForm.article_major"
            placeholder=""
            :options="options"
            :props="{ expandTrigger: 'hover' }"
            :value="value"
          ></el-cascader>
        </el-row>

        <el-row
          type="flex"
          justify="space-between"
          style="width: 600px; margin-bottom: 50px"
        >
          <font style="margin: 0 0 0 13px"></font>
          <el-select
            v-model="ruleForm.article_labels"
            ref="labelSelect"
            multiple
            filterable
            allow-create
            default-first-option
            placeholder="()"
            style="width: 400px"
            @input.native="filterData"
            @change="bindChange"
          >
            <el-option
              v-for="item in optionsSubject"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-row>

        <el-row type="flex" justify="space-between" style="width: 900px">
          <font><span>*&nbsp;</span></font>
          <el-form-item prop="article_introduce">
            <el-input
              type="textarea"
              style="width: 700px"
              class="public-uncheck"
              v-model="ruleForm.article_introduce"
              placeholder="()"
              clearable
              :autosize="{ minRows: 2 }"
              :maxlength="100"
              show-word-limit
            ></el-input>
          </el-form-item>
        </el-row>

        <el-row>
          <el-col :span="0.5">
            <font><span>*&nbsp;</span></font>
          </el-col>
        </el-row>

        <el-row type="flex" justify="space-between">
          <el-form-item prop="article_details">
            <mavon-editor
              style="min-height: 500px; max-height: 800px; width: 1100px"
              ref="md"
              @change="change"
              @imgAdd="imgAdd"
              v-model="ruleForm.article_details"
            />
          </el-form-item>
        </el-row>

        <el-row type="flex" justify="end" style="margin: 30px 60px 0 0">
          <div class="submit" @click="submitArticle()">
            <font></font>
          </div>
        </el-row>
      </div>
    </el-form>
  </div>
</template>

<script>
import { postArticleAPI, addArticleLabelAPI } from "@/api/articles";
import { uploadImageAPI } from "@/api/other";
import { mavonEditor } from "mavon-editor";
import "mavon-editor/dist/css/index.css";
export default {
  name: "PostArticle",
  data() {
    return {
      optionsSubject: [],
      value: [],
      options: [
        {
          value: "",
          label: "",
          children: [
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
          ],
        },
        {
          value: "",
          label: "",
          children: [
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
          ],
        },
        {
          value: "",
          label: "",
          children: [
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
          ],
        },
        {
          value: "",
          label: "",
          children: [
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
          ],
        },
        {
          value: "",
          label: "",
          children: [
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
            {
              value: "",
              label: "",
            },
          ],
        },
        {
          value: "",
          label: "",
        },
      ],
      ruleForm: {
        // 
        article_status: "1", //1-2-
        article_link: "",
        article_title: "",
        article_major: [],
        article_labels: [],
        article_introduce: "",
        article_details: "",
        article_md: "",
        article_uploaddate: "",
        article_updatedate: "",
        // id
        author_id: localStorage.getItem("user_info")
          ? JSON.parse(localStorage.getItem("user_info")).id
          : null,
      },
      html: "", // markdownhtml
      rules: {
        article_title: [
          { required: true, message: "", trigger: "blur" },
        ],
        article_details: [
          { required: true, message: "", trigger: "blur" },
        ],
        article_introduce: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        article_link: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
      },
    };
  },
  computed: {
    presentDate() {
      var date = new Date();
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var day = date.getDate();
      return year + "-" + this.addZero(month) + "-" + this.addZero(day);
    },
  },
  methods: {
    bindChange(e) {
      if (e.length > 5) {
        this.$message.warning("~");
        this.ruleForm.article_labels.splice(-1);
      }
    },
    addZero(s) {
      return s < 10 ? "0" + s : s;
    },
    filterData() {
      var str = this.$refs.labelSelect.$data.selectedLabel; // 
      if (str.length > 20) {
        this.$refs.labelSelect.$data.selectedLabel = str.substr(0, 21);
      }
    },
    // jsbase64
    dealImage(base64, w, callback) {
      var newImage = new Image();
      var quality = 0.9; //0-1
      newImage.src = base64;
      newImage.setAttribute("crossOrigin", "Anonymous"); //url
      var imgWidth, imgHeight;
      newImage.onload = function () {
        imgWidth = this.width;
        imgHeight = this.height;
        var canvas = document.createElement("canvas");
        var ctx = canvas.getContext("2d");
        if (Math.max(imgWidth, imgHeight) > w) {
          canvas.width = w;
          canvas.height = (w * imgHeight) / imgWidth;
        } else {
          canvas.width = imgWidth;
          canvas.height = imgHeight;
          quality = 0.9;
        }
        ctx.clearRect(0, 0, canvas.width, canvas.height);
        ctx.drawImage(this, 0, 0, canvas.width, canvas.height);
        var base64 = canvas.toDataURL("image/jpeg", quality); //
        // // ,50-150kbquality
        // while (base64.length / 1024 > 150) {
        //   quality -= 0.01;
        //   base64 = canvas.toDataURL("image/jpeg", quality);
        // }
        // // quality
        // while (base64.length / 1024 < 50) {
        //   quality += 0.001;
        //   base64 = canvas.toDataURL("image/jpeg", quality);
        // }
        callback(base64); //
      };
    },
    // 
    change(_, render) {
      // render  markdown [html]
      this.html = render;
    },
    imgAdd(pos, file) {
      this.dealImage(file.miniurl, 800, (results) => {
        uploadImageAPI({ imgData: results }).then((res) => {
          console.log(res.data.base64Data);
          if (res.data.result_code === 0) {
            this.$refs.md.$img2Url(pos, res.data.imgURL);
          }
        });
      });
    },
    submitArticle() {
      if (
        (this.ruleForm.article_status == "1" &&
          this.ruleForm.article_link &&
          this.ruleForm.article_title &&
          this.ruleForm.article_major &&
          this.ruleForm.article_introduce &&
          this.ruleForm.article_details) ||
        (this.ruleForm.article_status == "2" &&
          this.ruleForm.article_title &&
          this.ruleForm.article_major &&
          this.ruleForm.article_introduce &&
          this.ruleForm.article_details)
      ) {
        // ruleForm
        this.ruleForm.article_md = this.ruleForm.article_details;
        this.ruleForm.article_details = this.html;
        this.ruleForm.article_uploaddate = this.presentDate;
        this.ruleForm.article_updatedate = this.presentDate;
        // 
        let article_labels = [];
        if (localStorage.getItem("article_labels")) {
          for (
            var i = 0;
            i < JSON.parse(localStorage.getItem("article_labels")).length;
            i++
          ) {
            article_labels.push(
              JSON.parse(localStorage.getItem("article_labels"))[i]
            );
          }
        }
        let same = [];
        article_labels.forEach((item1) => {
          this.ruleForm.article_labels.forEach((item2) => {
            if (item1.value == item2) {
              console.log(item2);
              same.push(item2);
            }
          });
        });
        if (same.length != 0) {
          // 
          let result = this.ruleForm.article_labels.filter((item1) => {
            return same.every((item2) => {
              return item1 != item2;
            });
          });
          // result
          result.forEach((item) => {
            article_labels.push({
              label: item,
              value: item,
            });
          });
          // 
          result.forEach((item) => {
            addArticleLabelAPI({ label_name: item }).then((res) => {
              console.log(res.data);
            });
          });
        } else {
          this.ruleForm.article_labels.forEach((item) => {
            article_labels.push({
              label: item,
              value: item,
            });
            addArticleLabelAPI({ label_name: item }).then((res) => {
              console.log(res.data);
            });
          });
        }
        localStorage.setItem("article_labels", JSON.stringify(article_labels));
        console.log(this.ruleForm);
        postArticleAPI(this.ruleForm).then((res) => {
          console.log(res.data);
          if (localStorage.getItem("not_saved_article_info")) {
            localStorage.removeItem("not_saved_article_info");
          }
        });

        // 
        this.ruleForm = {
          article_link: "",
          article_title: "",
          article_major: [],
          article_labels: [],
          article_introduce: "",
          article_details: "",
          article_md: "",
          article_uploaddate: "",
          article_updatedate: "",
          author_id: localStorage.getItem("user_info")
            ? JSON.parse(localStorage.getItem("user_info")).id
            : null,
        };
        this.$notify({
          title: "",
          message: "",
          type: "success",
        });
      } else {
        this.$notify({
          title: "",
          message: "",
          type: "error",
        });
      }
    },
  },
  watch: {
    ruleForm: {
      handler: function (newV, _) {
        localStorage.setItem("not_saved_article_info", JSON.stringify(newV));
      },
      deep: true,
    },
  },
  mounted() {
    if (localStorage.getItem("not_saved_article_info")) {
      this.$confirm("", "", {
        distinguishCancelAndClose: true,
        confirmButtonText: "",
        cancelButtonText: "",
      })
        .then(() => {
          this.ruleForm = {
            ...JSON.parse(localStorage.getItem("not_saved_article_info")),
          };
        })
        .catch(() => {
          localStorage.removeItem("not_saved_article_info");
        });
    }
    if (localStorage.getItem("article_labels")) {
      for (
        var i = 0;
        i < JSON.parse(localStorage.getItem("article_labels")).length;
        i++
      ) {
        this.optionsSubject.push(
          JSON.parse(localStorage.getItem("article_labels"))[i]
        );
      }
    }
  },
  components: { mavonEditor },
};
</script>
<style scoped>
.PostArticle-wrap {
  margin-bottom: 30px;
  padding: 20px;
  width: 1300px;
  border-radius: 5px;
  background: #ffffff;
}
.title {
  font-family: SourceHanSansCN-Bold;
  font-size: 36px;
  font-weight: bold;
  color: #3d3d3d;
}
.title span {
  font-family: SourceHanSansCN-Bold;
  font-size: 16px;
  font-weight: normal;
  color: #9e9e9e;
}
.forms {
  margin: 0 0 0 100px;
}
.forms > * {
  margin: 20px 0 20px 0;
}
.forms > * font {
  font-family: SourceHanSansCN-Regular;
  font-size: 18px;
  font-weight: normal;
  color: #3d3d3d;
}
.forms > * font span {
  color: #ff8200;
}
.submit {
  width: 150px;
  height: 60px;
  border-radius: 30px;
  background: #76fff5;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: all 0.5s;
}
.submit font {
  font-family: SourceHanSansCN-Regular;
  font-size: 18px;
  font-weight: normal;
  line-height: 18px;
  color: #3d3d3d;
  transition: all 0.5s;
}
.submit:hover {
  background-color: #00ead8;
}
.submit:hover font {
  color: #ffffff;
}
.avatar-uploader {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
::v-deep .el-textarea__inner {
  display: block;
  resize: vertical;
  padding: 5px 15px;
  line-height: 1.5;
  box-sizing: border-box;
  width: 100%;
  font-size: inherit;
  color: #606266;
  background-color: #ffffff;
  background-image: none;
  border: 1px solid #dcdfe6;
  transition: border 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  border-radius: 5px;
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB",
    "Microsoft YaHei", "", Arial, sans-serif;
}
</style>